import { GoogleMap, Marker } from "@react-google-maps/api";
import { useRef, useState } from "react";

export default function PropertiesMap({ markers }: { markers: any }) {
  const mapRef = useRef(null);

  const [bounds, setBounds] = useState(null);

  // Store map instance on load
  const onMapLoad = (map: any) => {
    mapRef.current = map; // Save map instance to ref
  };

  const defaultMapContainerStyle = {
    width: "100%",
    height: "800px",
  };

  // Define custom map options
  const mapOptions = {
    styles: [
      {
        featureType: "poi",
        elementType: "labels",
        stylers: [{ visibility: "off" }],
      },
    ],
    disableDefaultUI: true, // Disable default map UI elements
    zoomControl: true, // Enable zoom control
  };

  const handleBoundsChanged = () => {
    if (mapRef.current) {
      const newBounds = mapRef.current.getBounds();
      //setBounds(newBounds);
      console.log("Bounds changed:", newBounds?.toString());
    }
  };

  return (
    <>
      <GoogleMap
        mapContainerStyle={defaultMapContainerStyle}
        zoom={13}
        center={{ lat: 21.178574, lng: 72.814149 }}
        options={mapOptions}
        onBoundsChanged={handleBoundsChanged}
        onLoad={onMapLoad} // Set map instance
      >
        {markers.map((marker: any) => (
          <Marker {...marker} onRightClick={() => {}} />
        ))}
      </GoogleMap>
    </>
  );
}
