import { Property } from "@/services/dtos/property-dto";
import http from "./api";

export function useProperties() {
  return {
    async getAll(
      publisherId: number,
      page: number,
      pageSize: number
    ): Promise<{
      properties: Property;
      record: number;
      totalPages: number;
      totals: number;
    }> {
      return http.get(
        `properties/${publisherId}?page=${page}&pageSize=${pageSize}`
      );
    },
  };
}
